@import '../../assets/css/variable.less';
*{box-sizing: border-box;}
.home{
  background: url('./img/home-bg.svg');
  .first-screen{
    height: calc(100vh - 72px);
    .title{
      padding-top: 278px;
      margin-left: 168px;
      .title-text{
        font-size: 72px;
        color: #FFFFFF;
      }
      .deri-describe{
        font-size: 24px;
      }
      .button-home{
        margin-top: 69px;
        .start-trading,.read-our{
          display: inline-block;
          width: 256px;
          height: 64px;
          border-radius: 4px;
          text-align: center;
          line-height: 64px;
          font-size: 18px;
          background-color: #3756CD;
        }
        .read-our{
          margin-left: 48px;
          background: rgba(85, 119, 253, 0.1);
        }
      }
    }
  }
}